.CreateTodoButton {
    background-color: #2fac97;
    box-shadow: 0px 5px 25px rgba(47,172,151, 0.5);
    border: none;
    border-radius: 50%;
    cursor: pointer;
    font-size: 2rem;
    position: fixed;
    bottom: 24px;
    right: 24px;
    font-weight: bold;
    color: #FAFAFA;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 55px;
    width: 55px;
  
    transform: rotate(0);
    transition: .3s ease;
    z-index: 1;
} 
  
.CreateTodoButton:hover {
    transform: rotate(224deg);
}